.fancybox__backdrop {
	background: rgba(240, 251, 247, 0.8) !important;
}

.wrapper {
	display: flex;
	flex-direction: column;
	height: 100%;

	.main {
		flex: 1 1 auto;
	}
}

.container {
	width: 1150px;
	padding: 0 15px;
	margin: 0 auto;
	max-width: 100%;

	@media (max-width: 1199px) {
		padding: 0 48px;
	}

	@media (max-width: 991px) {
		padding: 0 32px;
	}

	@media (max-width: 575px) {
		padding: 0 12px;
	}
}

h2 {
	pointer-events: none;
	position: relative;
	font-family: 'Raleway';
	font-style: normal;
	font-weight: 700;
	font-size: 40px;
	line-height: 130%;
	text-transform: uppercase;
	color: #222222;

	@media (max-width: 1199px) {
		font-size: 38px;
	}

	@media (max-width: 575px) {
		font-size: 28px;
	}

	span.title-text {
		display: block;
	}

	&.show {
		span.title-text {
			display: block;
			transform: translate(0, 100%);
			opacity: 0;
		}

		.line {
			position: absolute;
			top: 0;
			left: 0;
			width: 0%;
			height: 1px;
			background-color: #60B3A3;
			display: block;
		}

		&.show-active {
			span.title-text {
				transform: translate(0, 0);
				opacity: 1;
				transition: transform 1.8s ease 0.4s, opacity 1.8s ease 0.4s;
			}

			.line {
				width: 100%;
				transition: width 1.0s ease 0.2s;
			}
		}
	}
}

h3 {
	font-family: 'Raleway';
	font-style: normal;
	font-weight: 700;
	font-size: 28px;
	line-height: 140%;
	text-transform: uppercase;
	color: #222222;
	pointer-events: none;

	@media (max-width: 575px) {
		font-size: 22px;
	}
}

p,
li,
.fancybox__caption {
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 145%;
	color: #5E5E5E;

	@media (max-width: 575px) {
		font-size: 14px;
	}
}

.fancybox__caption {
	pointer-events: none;
	line-height: 145% !important;
	color: #5E5E5E !important;

	@media (max-width: 575px) {
		font-size: 14px;
	}
}

.btn-green {
	cursor: pointer;
	background: #FCFCFC;
	border: 1px solid #60B3A3;
	border-radius: 50px;
	padding: 12px 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	display: inline-block;
	transition: all 0.3s ease 0s;
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 145%;
	color: #5E5E5E;

	&:hover {
		box-shadow: 0px 0px 0px 3px rgba(96, 179, 163, 0.5);
	}

	&.active {
		background: #60B3A3;
		color: #EAEAEA;
	}
}

.btn-orange {
	cursor: pointer;
	padding: 20px 40px;
	background: #CE7A74;
	border-radius: 50px;
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 145%;
	text-align: center;
	color: #FCFCFC;
}

.btn-white {
	cursor: pointer;
	padding: 20px 40px;
	border: 1px solid #CE7A74;
	border-radius: 50px;
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 145%;
	text-align: center;
	color: #222222;
}

.card,
.fancybox__content {
	background-color: #F0FBF7 !important;
	border: 1px solid #60B3A3;
	border-radius: 20px;

	&__title {
		font-family: 'Raleway';
		font-style: normal;
		font-weight: 700;
		font-size: 28px;
		line-height: 140%;
		text-transform: uppercase;
		color: #222222;
	}

	>span {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 145%;
		color: #5E5E5E;
	}
}

.fancybox__content {
	width: auto !important;
	height: auto !important;
	padding: 40px !important;

	@media (max-width: 575px) {
		padding: 40px 16px;
	}

	img {
		width: 100%;
		height: 100%;
	}
}

.form {
	background-color: #FCFCFC;
	padding: 40px;

	@media (max-width: 575px) {
		padding: 40px 16px;
	}

	&__input {
		margin-top: 16px;

		&:first-child {
			margin: 0;
		}

		input {
			width: 100%;
			background: #FCFCFC;
			border: 1px solid #E7E7E7;
			border-radius: 2px;
			padding: 16px;
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 400;
			font-size: 14px;
			line-height: 145%;
			color: #A4A4A4;
		}
	}

	&__agreement {
		margin-top: 16px;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 145%;
		color: #5E5E5E;
		display: flex;
		align-items: center;

		@media (max-width: 575px) {
			align-items: flex-start;
		}

		&-agree {
			cursor: pointer;
			min-width: 16px;
			min-height: 16px;
			width: 16px;
			height: 16px;
			display: block;
			background: #FCFCFC;
			border: 1px solid #A4A4A4;
			border-radius: 4px;
			position: relative;
			transition: all 0.3s ease 0s;

			&::before {
				content: '';
				transition: all 0.3s ease 0s;
				position: absolute;
				top: 50%;
				left: 50%;
				width: 9px;
				height: 6.5px;
				transform: translate(-50%, -50%);
				opacity: 0;
				background: url('../images/calculator/check.svg') 0 0 / 100% 100% no-repeat;
			}

			&:hover {
				box-shadow: 0px 0px 0px 3px rgba(2, 201, 141, 0.15);
			}

			&.active {
				&::before {
					opacity: 1;
				}
			}
		}

		p {
			margin-left: 10px;

			a {
				text-decoration-line: underline;
			}
		}
	}

	&__btn {
		margin-top: 40px;
	}
}

.spoiler {
	&__title {
		cursor: pointer;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 500;
		font-size: 20px;
		line-height: 145%;
		color: #222222;
		display: flex;
		justify-content: space-between;
		align-items: center;

		@media (max-width: 575px) {
			font-size: 18px;
		}

		span {
			display: block;
			width: 40px;
			height: 40px;
			min-width: 40px;
			min-height: 40px;
			margin-left: 24px;
		}
	}

	&__content {
		margin-top: 24px;
		display: none;

		@media (max-width: 575px) {
			margin-top: 16px;
		}

		&.active {
			display: block;
		}
	}
}

.fancybox__slide {
	padding: 0 40px !important;
}

.fancybox__content {
	width: 544px !important;
	max-width: 100% !important;
	padding: 80px 40px 40px !important;

	@media (max-width: 575px) {
		padding: 80px 16px 40px !important;
	}

	.carousel__button {
		&.is-close {
			width: 24px;
			height: 24px;
			top: 40px !important;
			right: 40px !important;
			color: #60B3A3 !important;
		}
	}
}

.fancybox__nav {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 1150px;
	padding: 0 15px;
	max-width: 100%;
	transform: translate(-50%, -50%);
	display: flex;
	justify-content: space-between;
	align-items: center;

	@media (max-width: 1199px) {
		padding: 0 48px;
	}

	@media (max-width: 991px) {
		padding: 0 10px;
	}

	.carousel__button {
		background: #FCFCFC;
		border: 1px solid #60B3A3;
		border-radius: 100px;
		position: static !important;
		transform: translate(0, 0) !important;
		width: 60px;
		height: 60px;

		@media (max-width: 991px) {
			width: 24px;
			height: 24px;
		}

		svg {
			display: none;
		}

		&::before {
			content: '';
			width: 100%;
			height: 100%;
			background: url('../images/arrow.svg') center / 13.5px 27px no-repeat;

			@media (max-width: 991px) {
				background: url('../images/arrow.svg') center / 5.4px 10.8px no-repeat;
			}
		}

		&.is-prev {
			order: -1;

			&::before {
				transform: rotate(180deg);
			}
		}
	}
}