@charset "UTF-8";
/* base */
/* media */
/* шрифты */
/* цвета */
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, a:active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

a {
  color: inherit;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

/* mixin */
/* автоматическая сетка на flex */
/* адаптив */
/*libs*/
/* main */
.fancybox__backdrop {
  background: rgba(240, 251, 247, 0.8) !important;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper .main {
  flex: 1 1 auto;
}

.container {
  width: 1150px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 48px;
  }
}
@media (max-width: 991px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 575px) {
  .container {
    padding: 0 12px;
  }
}

h2 {
  pointer-events: none;
  position: relative;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  color: #222222;
}
@media (max-width: 1199px) {
  h2 {
    font-size: 38px;
  }
}
@media (max-width: 575px) {
  h2 {
    font-size: 28px;
  }
}
h2 span.title-text {
  display: block;
}
h2.show span.title-text {
  display: block;
  transform: translate(0, 100%);
  opacity: 0;
}
h2.show .line {
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 1px;
  background-color: #60B3A3;
  display: block;
}
h2.show.show-active span.title-text {
  transform: translate(0, 0);
  opacity: 1;
  transition: transform 1.8s ease 0.4s, opacity 1.8s ease 0.4s;
}
h2.show.show-active .line {
  width: 100%;
  transition: width 1s ease 0.2s;
}

h3 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 140%;
  text-transform: uppercase;
  color: #222222;
  pointer-events: none;
}
@media (max-width: 575px) {
  h3 {
    font-size: 22px;
  }
}

p,
li,
.fancybox__caption {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 145%;
  color: #5E5E5E;
}
@media (max-width: 575px) {
  p,
li,
.fancybox__caption {
    font-size: 14px;
  }
}

.fancybox__caption {
  pointer-events: none;
  line-height: 145% !important;
  color: #5E5E5E !important;
}
@media (max-width: 575px) {
  .fancybox__caption {
    font-size: 14px;
  }
}

.btn-green {
  cursor: pointer;
  background: #FCFCFC;
  border: 1px solid #60B3A3;
  border-radius: 50px;
  padding: 12px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: inline-block;
  transition: all 0.3s ease 0s;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
}
.btn-green:hover {
  box-shadow: 0px 0px 0px 3px rgba(96, 179, 163, 0.5);
}
.btn-green.active {
  background: #60B3A3;
  color: #EAEAEA;
}

.btn-orange {
  cursor: pointer;
  padding: 20px 40px;
  background: #CE7A74;
  border-radius: 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 145%;
  text-align: center;
  color: #FCFCFC;
}

.btn-white {
  cursor: pointer;
  padding: 20px 40px;
  border: 1px solid #CE7A74;
  border-radius: 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 145%;
  text-align: center;
  color: #222222;
}

.card,
.fancybox__content {
  background-color: #F0FBF7 !important;
  border: 1px solid #60B3A3;
  border-radius: 20px;
}
.card__title,
.fancybox__content__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 140%;
  text-transform: uppercase;
  color: #222222;
}
.card > span,
.fancybox__content > span {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 145%;
  color: #5E5E5E;
}

.fancybox__content {
  width: auto !important;
  height: auto !important;
  padding: 40px !important;
}
@media (max-width: 575px) {
  .fancybox__content {
    padding: 40px 16px;
  }
}
.fancybox__content img {
  width: 100%;
  height: 100%;
}

.form {
  background-color: #FCFCFC;
  padding: 40px;
}
@media (max-width: 575px) {
  .form {
    padding: 40px 16px;
  }
}
.form__input {
  margin-top: 16px;
}
.form__input:first-child {
  margin: 0;
}
.form__input input {
  width: 100%;
  background: #FCFCFC;
  border: 1px solid #E7E7E7;
  border-radius: 2px;
  padding: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 145%;
  color: #A4A4A4;
}
.form__agreement {
  margin-top: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
  display: flex;
  align-items: center;
}
@media (max-width: 575px) {
  .form__agreement {
    align-items: flex-start;
  }
}
.form__agreement-agree {
  cursor: pointer;
  min-width: 16px;
  min-height: 16px;
  width: 16px;
  height: 16px;
  display: block;
  background: #FCFCFC;
  border: 1px solid #A4A4A4;
  border-radius: 4px;
  position: relative;
  transition: all 0.3s ease 0s;
}
.form__agreement-agree::before {
  content: "";
  transition: all 0.3s ease 0s;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 9px;
  height: 6.5px;
  transform: translate(-50%, -50%);
  opacity: 0;
  background: url("../images/calculator/check.svg") 0 0/100% 100% no-repeat;
}
.form__agreement-agree:hover {
  box-shadow: 0px 0px 0px 3px rgba(2, 201, 141, 0.15);
}
.form__agreement-agree.active::before {
  opacity: 1;
}
.form__agreement p {
  margin-left: 10px;
}
.form__agreement p a {
  text-decoration-line: underline;
}
.form__btn {
  margin-top: 40px;
}

.spoiler__title {
  cursor: pointer;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 145%;
  color: #222222;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 575px) {
  .spoiler__title {
    font-size: 18px;
  }
}
.spoiler__title span {
  display: block;
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  margin-left: 24px;
}
.spoiler__content {
  margin-top: 24px;
  display: none;
}
@media (max-width: 575px) {
  .spoiler__content {
    margin-top: 16px;
  }
}
.spoiler__content.active {
  display: block;
}

.fancybox__slide {
  padding: 0 40px !important;
}

.fancybox__content {
  width: 544px !important;
  max-width: 100% !important;
  padding: 80px 40px 40px !important;
}
@media (max-width: 575px) {
  .fancybox__content {
    padding: 80px 16px 40px !important;
  }
}
.fancybox__content .carousel__button.is-close {
  width: 24px;
  height: 24px;
  top: 40px !important;
  right: 40px !important;
  color: #60B3A3 !important;
}

.fancybox__nav {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1150px;
  padding: 0 15px;
  max-width: 100%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .fancybox__nav {
    padding: 0 48px;
  }
}
@media (max-width: 991px) {
  .fancybox__nav {
    padding: 0 10px;
  }
}
.fancybox__nav .carousel__button {
  background: #FCFCFC;
  border: 1px solid #60B3A3;
  border-radius: 100px;
  position: static !important;
  transform: translate(0, 0) !important;
  width: 60px;
  height: 60px;
}
@media (max-width: 991px) {
  .fancybox__nav .carousel__button {
    width: 24px;
    height: 24px;
  }
}
.fancybox__nav .carousel__button svg {
  display: none;
}
.fancybox__nav .carousel__button::before {
  content: "";
  width: 100%;
  height: 100%;
  background: url("../images/arrow.svg") center/13.5px 27px no-repeat;
}
@media (max-width: 991px) {
  .fancybox__nav .carousel__button::before {
    background: url("../images/arrow.svg") center/5.4px 10.8px no-repeat;
  }
}
.fancybox__nav .carousel__button.is-prev {
  order: -1;
}
.fancybox__nav .carousel__button.is-prev::before {
  transform: rotate(180deg);
}

/* components */
.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: -120%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  transition: all 0.3s ease 0s;
}
.popup__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(240, 251, 247, 0.8);
}
.popup__container {
  padding: 80px 40px;
  position: absolute;
  width: 544px;
  max-width: 100%;
  max-height: 100vh;
  overflow: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 400px) {
  .popup__container {
    border-radius: 0;
  }
}
.popup__container > span {
  display: block;
  margin-top: 16px;
}
.popup__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 140%;
  text-transform: uppercase;
  color: #222222;
}
.popup__close {
  cursor: pointer;
  position: absolute;
  top: 40px;
  right: 40px;
  width: 24px;
  height: 24px;
  background: url("../images/calculator/x.svg") 0 0 no-repeat;
}
.popup__form {
  margin-top: 40px;
}
.popup__input {
  background: #FCFCFC;
  border: 1px solid #E7E7E7;
  border-radius: 2px;
  margin-top: 16px;
}
.popup__input input,
.popup__input textarea {
  border-radius: 2px;
  padding: 16px;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
}
.popup__input input::placeholder,
.popup__input textarea::placeholder {
  color: #A4A4A4;
}
.popup__input textarea {
  resize: vertical;
  min-height: 128px;
  height: 128px;
  max-height: 200px;
}
.popup__agreement {
  margin-top: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
  display: flex;
}
.popup__agreement-agree {
  cursor: pointer;
  min-width: 16px;
  min-height: 16px;
  width: 16px;
  height: 16px;
  display: block;
  background: #FCFCFC;
  border: 1px solid #A4A4A4;
  border-radius: 4px;
  position: relative;
  transition: all 0.3s ease 0s;
}
.popup__agreement-agree::before {
  content: "";
  transition: all 0.3s ease 0s;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 9px;
  height: 6.5px;
  transform: translate(-50%, -50%);
  opacity: 0;
  background: url("../images/calculator/check.svg") 0 0/100% 100% no-repeat;
}
.popup__agreement-agree:hover {
  box-shadow: 0px 0px 0px 3px rgba(2, 201, 141, 0.15);
}
.popup__agreement-agree.active::before {
  opacity: 1;
}
.popup__agreement p {
  margin-left: 10px;
}
.popup__agreement p a {
  text-decoration-line: underline;
}
.popup__btn {
  width: 100%;
  cursor: pointer;
  margin-top: 40px;
}
.popup.active {
  top: 0;
}

/*blocks*/
.hero {
  position: relative;
}
.hero video {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero::before {
  content: "";
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.3;
}
.hero .container {
  position: relative;
  z-index: 1;
}
.hero__body {
  padding: 218px 0 234px;
  max-width: 544px;
}
@media (max-width: 1199px) {
  .hero__body {
    padding: 226px 0 232px;
  }
}
@media (max-width: 991px) {
  .hero__body {
    padding: 329px 0;
    max-width: 520px;
  }
}
@media (max-width: 575px) {
  .hero__body {
    padding: 132px 0 324px;
  }
}
.hero h1 {
  padding-bottom: 24px;
  position: relative;
}
.hero h1 span {
  display: block;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
  color: #FCFCFC;
  opacity: 0;
  transform: translate(0, 25px);
}
@media (max-width: 1199px) {
  .hero h1 span {
    font-size: 48px;
  }
}
@media (max-width: 575px) {
  .hero h1 span {
    font-size: 30px;
  }
}
.hero h1::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 1px;
  background-color: #FCFCFC;
}
.hero p {
  margin-top: 24px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 145%;
  color: #FCFCFC;
  transform: translate(0, 25px);
  opacity: 0;
}
@media (max-width: 575px) {
  .hero p {
    font-size: 18px;
    font-weight: 400;
  }
}
.hero.active h1 span {
  opacity: 1;
  transform: translate(0, 0);
  transition: opacity 1.8s ease 0.4s, transform 1.8s ease 0.4s;
}
.hero.active h1::before {
  transition: width 2.3s ease 0.4s;
  width: 100%;
}
.hero.active p {
  transform: translate(0, 0);
  opacity: 1;
  transition: opacity 1.8s ease 0.8s, transform 1.8s ease 0.8s;
}

.calculator {
  margin-top: 130px;
}
@media (max-width: 1199px) {
  .calculator {
    margin-top: 100px;
  }
}
@media (max-width: 575px) {
  .calculator {
    margin-top: 70px;
  }
}
.calculator__body {
  display: flex;
  margin: 64px -16px 0;
}
@media (max-width: 1199px) {
  .calculator__body {
