.calculator__btns {
  margin-top: 24px;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}
@media (max-width: 575px) {
  .calculator__btns {
    margin-top: 16px;
    margin-bottom: 0;
    flex-wrap: wrap;
  }
}
.calculator__btn {
  margin-right: 4px;
}
@media (max-width: 575px) {
  .calculator__btn {
    margin-top: 8px;
  }
}
.calculator__left {
  padding-top: 64px;
  padding-bottom: 64px;
}
@media (max-width: 991px) {
  .calculator__left {
    padding: 24px 16px;
  }
}
@media (max-width: 575px) {
  .calculator__left {
    padding: 0 0 24px;
  }
