.calculator__left-item {
  margin-top: 24px;
  padding-right: 64px;
}
@media (max-width: 575px) {
  .calculator__left-item {
    padding-right: 20px;
  }
}
.calculator__left-item:first-child {
  margin: 0;
}
.calculator__right {
  position: relative;
  height: 100%;
}
@media (max-width: 991px) {
  .calculator__right {
    margin-top: 24px;
  }
}
.calculator__subtitle {
  margin-bottom: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 145%;
  color: #222222;
}
@media (max-width: 575px) {
  .calculator__subtitle {
    font-size: 18px;
  }
}
.calculator__input {
  padding: 12px 36px 12px 10px;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 145%;
  color: #5E5E5E;
}
.calculator__input-wrapper {
  padding: 0;
  cursor: default;
  width: 100%;
  background: #FFFFFF;
  border-width: 1px 1px 3px 1px;
  border-style: solid;
  border-color: #60B3A3;
  border-radius: 8px;
  position: relative;
  box-shadow: none !important;
}
.calculator__input-wrapper::before {
  content: "";
  width: 10px;
  height: 5px;
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translate(0, -50%);
  background: url("../images/calculator/arrow.svg") 0 0/100% 100% no-repeat;
}
.calculator__select {
  position: relative;
}
.calculator__select .calculator__input-range {
  cursor: pointer;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
}
.calculator__select .calculator__input-range input[type=range] {
  -webkit-appearance: none !important;
  height: 0;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.calculator__select .calculator__input-range input[type=range]::-webkit-slider-thumb {
  cursor: pointer;
  -webkit-appearance: none !important;
  background: #60B3A3;
  height: 12px;
  width: 12px;
  border-radius: 50%;
}
.calculator__select-fromto {
  margin-top: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calculator__select-fromto span {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
}
.calculator__dropdown {
  display: none;
  position: absolute;
  z-index: 99;
  top: 50px;
  left: 0;
  max-height: 168px;
  overflow: auto;
  width: 100%;
  background: #F0FBF7;
  border: 1px solid #60B3A3;
  box-shadow: 0px 4px 8px rgba(2, 201, 141, 0.08);
  border-radius: 8px;
}
.calculator__dropdown::-webkit-scrollbar-thumb {
  background-color: #60B3A3 !important;
}
.calculator__dropdown ul {
  padding: 4px 0;
  display: block;
}
.calculator__dropdown ul li {
  cursor: pointer;
  width: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 145%;
  color: #5E5E5E;
  padding: 0 16px;
  transition: all 0.3s ease 0s;
}
.calculator__dropdown ul li:hover {
  background: #60B3A3;
  color: #EAEAEA;
